import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import ImageRequirementsForm from "../components/image/ImageRequirementsForm";
import FeatureGrid from "../components/ui/FeatureGrid";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from "../utils/stepsConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import Schema from "../components/seo/Schema";
import schemaConfig from "../utils/schemaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";


export default function Resize() {

  const features = featuresDescriptionConfig.resize

  const resizeSteps = stepsConfig.resize
  const meta = metaConfig.resize
  const schema = schemaConfig.resize

  return (
    <>
      <Meta {...meta} />
      <Schema schema={schema} />
      <div className="max-w-5xl mx-auto px-4 py-10">

        <AdSlot slot={adSlots.resize[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Resize</h1>
        <p className="text-center text-gray-600 mb-8">Upload an image and set width/height.</p>

        <ImageProcessorSection
          downloadName="resized-image"
        >
          {({ onApply, loading }) => (
            <>
              <ImageRequirementsForm onSubmit={onApply} showWidth={true} showHeight={true} showFormat={true} showTargetKB={false} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.resize[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.resize[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={resizeSteps} title="Resize in 4 Easy Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.resize[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

