import React, { useState } from 'react';
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";

const FAQ = () => {
  const [openItems, setOpenItems] = useState(new Set());

  const toggleItem = (index) => {
    const newOpenItems = new Set(openItems);
    if (newOpenItems.has(index)) {
      newOpenItems.delete(index);
    } else {
      newOpenItems.add(index);
    }
    setOpenItems(newOpenItems);
  };

  const faqData = [
    {
      category: "General Questions",
      questions: [
        {
          question: "Is this image resizer completely free to use?",
          answer: "Yes! Our image resizer is 100% free to use. There are no hidden fees, subscriptions, or watermarks. We've been providing free image resizing services and have processed multiple images for users worldwide."
        },
        {
          question: "Do I need to create an account or register?",
          answer: "No registration required! You can use our image resizer immediately without creating an account, providing personal information, or going through any signup process. Simply upload your image and start resizing."
        },
        {
          question: "What file formats are supported?",
          answer: "We support all major image formats including JPG/JPEG, PNG, and WebP. You can upload images in any of these formats and convert between them as needed."
        },
        {
          question: "Is my data secure and private?",
          answer: "Absolutely! Your images are uploaded via a secure 256-bit encrypted SSL connection and are automatically deleted from our servers within few hours. We never store your images permanently or use them for any other purpose."
        }
      ]
    },
    {
      category: "Technical Questions",
      questions: [
        {
          question: "What's the maximum file size I can upload?",
          answer: "You can upload images up to 25MB in size. This covers most high-resolution photos and professional images. If your file is larger, we recommend compressing it first using our compression tool."
        },
        {
          question: "What are the maximum dimensions I can resize to?",
          answer: "You can resize images up to 8000x8000 pixels. This is more than sufficient for most use cases including high-resolution displays, print media, and professional applications."
        },
        {
          question: "How does the quality preservation work?",
          answer: "Our advanced algorithms use intelligent resampling techniques to maintain image quality during resizing. We automatically choose the best interpolation method based on whether you're scaling up or down, ensuring sharp, clear results."
        },
        {
          question: "Can I maintain aspect ratio while resizing?",
          answer: "Yes! When you enter only width or height, our tool automatically calculates the other dimension to maintain the original aspect ratio. You can also manually set both dimensions if you need specific proportions."
        }
      ]
    },
    {
      category: "Platform-Specific Questions",
      questions: [
        {
          question: "What are the best dimensions for Amazon product images?",
          answer: "Amazon recommends at least 1600px on the longest side, with 2000px being ideal. Our Amazon Product Image Resizer automatically applies these optimal settings. Images should be in JPG format with a white background for best results."
        },
        {
          question: "What sizes work best for LinkedIn images?",
          answer: "For LinkedIn: Profile photos should be 400x400px, cover images 1584x396px, and post images 1200x1200px. Our LinkedIn Image Resizer has preset buttons for all these sizes to make it easy."
        },
        {
          question: "What are the optimal Shopify image dimensions?",
          answer: "Shopify works best with: Product images at 2048x2048px, thumbnails at 1024x1024px, and social sharing images at 1200x628px. Our Shopify Image Maker includes presets for all these formats."
        },
        {
          question: "What passport photo sizes are supported?",
          answer: "We support US (2x2 inches/600x600px), EU (35x45mm/827x1063px), and Indian (35x45mm/413x531px) passport photo formats. Each format meets official government requirements for passport applications."
        }
      ]
    },
    {
      category: "Usage Questions",
      questions: [
        {
          question: "Can I process multiple images at once?",
          answer: "Currently, our tool processes one image at a time to ensure the highest quality results. However, you can quickly process multiple images by uploading them sequentially - the process is very fast!"
        },
        {
          question: "How long does image processing take?",
          answer: "Most images are processed in under 5 seconds! Our cloud-based processing is extremely fast, even for high-resolution images. Processing time depends on file size and complexity, but it's typically very quick."
        },
        {
          question: "Can I undo changes or revert to original?",
          answer: "Yes! Simply upload your original image again to start fresh. We don't modify your original file - we create a new processed version, so your original image remains unchanged on your device."
        },
        {
          question: "What if I'm not satisfied with the results?",
          answer: "You can easily adjust the settings and reprocess your image. Try different dimensions, compression levels, or formats until you get the perfect result. There's no limit to how many times you can process an image."
        }
      ]
    },
    {
      category: "Browser & Compatibility",
      questions: [
        {
          question: "Which browsers are supported?",
          answer: "Our image resizer works on all modern browsers including Chrome, Firefox, Safari, Edge, and Opera. It's also fully compatible with mobile browsers on iOS and Android devices."
        },
        {
          question: "Do I need to install any software?",
          answer: "No software installation required! Our tool is 100% browser-based and works entirely in your web browser. It's compatible with Windows, Mac, Linux, and mobile devices."
        },
        {
          question: "Will it work on my mobile device?",
          answer: "Yes! Our image resizer is fully responsive and works perfectly on smartphones and tablets. The interface automatically adapts to your screen size for the best mobile experience."
        },
        {
          question: "Do I need a fast internet connection?",
          answer: "A standard internet connection is sufficient for most images. For very large files (over 10MB), a faster connection will improve upload speed, but processing happens on our servers so it doesn't affect your local connection."
        }
      ]
    }
  ];

  return (
    <div className="max-w-4xl mx-auto px-4 py-10">
      <div className="text-center mb-12">
        <h1 className="text-4xl sm:text-5xl font-bold text-gray-900 mb-4">
          Frequently Asked Questions
        </h1>
        <p className="text-xl text-gray-600 max-w-3xl mx-auto">
          Find answers to common questions about our free online image resizer, compressor, and converter tools.
        </p>
      </div>

      <div className="space-y-8">
        {faqData.map((category, categoryIndex) => (
          <div key={categoryIndex} className="bg-white rounded-2xl shadow-md border">
            <div className="p-6 border-b border-gray-200">
              <h2 className="text-2xl font-bold text-gray-900">{category.category}</h2>
            </div>
            <div className="divide-y divide-gray-200">
              {category.questions.map((item, itemIndex) => {
                const globalIndex = `${categoryIndex}-${itemIndex}`;
                const isOpen = openItems.has(globalIndex);
                
                return (
                  <div key={itemIndex} className="p-6">
                    <button
                      onClick={() => toggleItem(globalIndex)}
                      className="w-full text-left flex justify-between items-start group"
                    >
                      <h3 className="text-lg font-semibold text-gray-900 group-hover:text-blue-600 transition-colors pr-4">
                        {item.question}
                      </h3>
                      <div className="flex-shrink-0 ml-4">
                        <svg
                          className={`w-5 h-5 text-gray-500 transform transition-transform duration-200 ${
                            isOpen ? 'rotate-180' : ''
                          }`}
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M19 9l-7 7-7-7"
                          />
                        </svg>
                      </div>
                    </button>
                    {isOpen && (
                      <div className="mt-4 pr-8">
                        <p className="text-gray-600 leading-relaxed">{item.answer}</p>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        ))}
      </div>

      <div className="mt-16 bg-blue-50 rounded-2xl p-8 text-center">
        <h3 className="text-2xl font-bold text-gray-900 mb-4">
          Still have questions?
        </h3>
        <p className="text-gray-600 mb-6">
          Can't find the answer you're looking for? We're here to help!
        </p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <a
            href="mailto:contact@attobite.com"
            className="inline-flex items-center px-6 py-3 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors"
          >
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
            </svg>
            Contact Support
          </a>
          <a
            href="/blog"
            className="inline-flex items-center px-6 py-3 bg-white text-blue-600 font-medium rounded-lg border border-blue-200 hover:bg-blue-50 transition-colors"
          >
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 20H5a2 2 0 01-2-2V6a2 2 0 012-2h10a2 2 0 012 2v1m2 13a2 2 0 01-2-2V7m2 13a2 2 0 002-2V9a2 2 0 00-2-2h-2m-4-3H9M7 16h6M7 8h6v4H7V8z" />
            </svg>
            Read Our Blog
          </a>
        </div>
      </div>
    </div>
  );
};

export default FAQ;
