import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from "../utils/stepsConfig";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

const SpotifyForm = ({ onSubmit }) => (
  <div className="grid gap-3 sm:grid-cols-3 mt-6">
    <button onClick={() => onSubmit({ width: 640, height: 640, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Album 640x640</button>
    <button onClick={() => onSubmit({ width: 3000, height: 3000, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">High-res 3000x3000</button>
    <button onClick={() => onSubmit({ width: 1080, height: 1080, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Canvas 1080x1080</button>
  </div>
);

export default function SpotifyImageMaker() {
  const features = featuresDescriptionConfig.spotify

  const spotifySteps = stepsConfig.spotify

  const meta = metaConfig.spotify;

  return (
    <>
      <Meta {...meta} />

      <div className="max-w-5xl mx-auto px-4 py-10">

        <AdSlot slot={adSlots.spotify[0]} style={{ margin: "20px 0" }} />

        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Spotify Image Maker</h1>
        <p className="text-center text-gray-600 mb-8">Generate Spotify-style cover images.</p>

        <ImageProcessorSection downloadName="spotify-image">
          {({ onApply, loading }) => (
            <>
              <SpotifyForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.spotify[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.spotify[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={spotifySteps} title="Make Spotify Covers in 4 Easy Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.spotify[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

