import React from "react";

const ProcessSteps = ({ steps, title = "How It Works" }) => {
  return (
    <div className="max-w-2xl mx-auto mt-12">
      <h2 className="text-2xl font-bold text-center mb-8">{title}</h2>

      <div className="relative">
        {/* Vertical line */}
        <div className="absolute left-5 top-0 h-full w-0.5 bg-blue-300"></div>

        {steps.map((step, index) => (
          <div
            key={index}
            className={`relative flex items-start mb-8 ${index === steps.length - 1 ? "mb-0" : ""}`}
          >
            <div className="flex-shrink-0 w-10 h-10 rounded-full bg-blue-500 text-white flex items-center justify-center z-10">
              {index + 1}
            </div>
            <div className="ml-6">
              <h3 className="font-semibold text-lg">{step.title}</h3>
              <p className="text-gray-600 text-sm">{step.description}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProcessSteps;
