import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import ReactMarkdown from "react-markdown";
import matter from "gray-matter";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import Schema from "../components/seo/Schema";
import schemaConfig from "../utils/schemaConfig";

export default function BlogPost() {
  const { slug } = useParams();
  const [post, setPost] = useState(null);

  useEffect(() => {
    import(`../blog/posts/${slug}.md`)
      .then((res) => fetch(res.default))
      .then((r) => r.text())
      .then((text) => {
        const { data, content } = matter(text); //  split metadata & content
        setPost({
          ...data,
          content,
        });
      })
      .catch((err) => console.error("Error loading markdown:", err));
  }, [slug]);

  if (!post) {
    return <h2 className="text-center text-red-500">Loading...</h2>;
  }

  const meta = {
    title: post.title,
    description: post.description,
    keywords: post.keywords || "",
  };

  const schema = schemaConfig.blogPost({
    title: post.title,
    description: post.description,
    date: post.date,
    url: window.location.href,
  });

  return (
    <>
      {/* SEO */}
      <Meta {...meta} />
      <Schema schema={schema} />
      <div className="max-w-3xl mx-auto p-6 bg-white rounded-xl shadow">


        {/* Blog Post */}
        <h1 className="text-3xl font-bold mb-4">{post.title}</h1>
        <p className="text-gray-500 mb-6">{post.date}</p>

        <ReactMarkdown
          components={{
            h1: (props) => <h1 className="text-2xl font-bold my-4" {...props} />,
            h2: (props) => <h2 className="text-xl font-semibold my-3" {...props} />,
            p: (props) => <p className="text-gray-700 leading-7 mb-4" {...props} />,
            a: (props) => (
              <a className="text-blue-600 underline hover:text-blue-800" {...props} />
            ),
            ul: (props) => <ul className="list-disc pl-6 mb-4" {...props} />,
            li: (props) => <li className="mb-1 text-gray-700" {...props} />,
          }}
        >
          {post.content}
        </ReactMarkdown>
      </div>
    </>

  );
}
