// src/pages/Home.js
import React, { useState, useEffect } from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import ImageRequirementsForm from "../components/image/ImageRequirementsForm";
import FeatureGrid from "../components/ui/FeatureGrid";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import schemaConfig from "../utils/schemaConfig";
import Schema from "../components/seo/Schema";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

const Home = () => {
  // Home uses the generic processor with all options visible
  const features = featuresDescriptionConfig.home
  const homeSteps = stepsConfig.home
  const meta = metaConfig.home
  console.log(meta)
  const schema = schemaConfig.home

  return (
    <>
      <Schema schema={schema} />
      <Meta {...meta} />
      <div className="max-w-5xl mx-auto px-4 py-10">
        <AdSlot slot={adSlots.home[0]} style={{ margin: "20px 0" }} />

        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">
          The all-in-one online tool to resize, compress, and convert images  fast, free, and secure.
        </h1>
        <p className="text-center text-gray-600 mb-8">
          Upload, set target size, change format (JPG/PNG/WebP), and download instantly.
        </p>

        <ImageProcessorSection downloadName="processed-image">
          {({ onApply, loading }) => (
            <>
              <ImageRequirementsForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.home[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.home[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={homeSteps} title="Edit Images in 4 Simple Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.home[3]} style={{ marginTop: "30px" }} />
      </div>

    </>

  );
};

export default Home;
