import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import ImageRequirementsForm from "../components/image/ImageRequirementsForm";
import FeatureGrid from "../components/ui/FeatureGrid";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import schemaConfig from "../utils/schemaConfig";
import Schema from "../components/seo/Schema";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

export default function Compress() {
  const features = featuresDescriptionConfig.compress

  const steps = stepsConfig.compress
  const meta = metaConfig.compress
  const schema = schemaConfig.compress

  return (

    <>
      <Meta {...meta} />
      <Schema schema={schema} />
      <div className="max-w-5xl mx-auto px-4 py-10">
      <AdSlot slot={adSlots.compress[0]} style={{ margin: "20px 0" }} />

        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Compress</h1>
        <p className="text-center text-gray-600 mb-8">Set a target size (KB) and optionally change format.</p>

        <ImageProcessorSection downloadName="compressed-image">
          {({ onApply, loading }) => (
            <>
              <ImageRequirementsForm onSubmit={onApply} showWidth={false} showHeight={false} showFormat={true} showTargetKB={true} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.compress[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.compress[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={steps} title="Compress Images in 4 Easy Steps" />

        {/* Bottom Ad */}
        <AdSlot slot={adSlots.compress[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

