// src/utils/schemaConfig.js

const schemaConfig = {
    home: {
      "@context": "https://schema.org",
      "@type": "WebSite",
      name: "TheImageResizer",
      url: "https://theimageresizer.com",
      description: "Free online tool to resize, compress, convert, and crop images instantly. Secure, fast, and watermark-free.",
      potentialAction: {
        "@type": "SearchAction",
        target: "https://theimageresizer.com/search?q={search_term_string}",
        "query-input": "required name=search_term_string"
      }
    },
  
    resize: {
      "@context": "https://schema.org",
      "@type": "SoftwareApplication",
      name: "Online Image Resizer",
      operatingSystem: "All",
      applicationCategory: "MultimediaApplication",
      offers: { "@type": "Offer", price: "0", priceCurrency: "USD" },
      url: "https://theimageresizer.com/resize",
      description: "Free tool to resize JPG, PNG, and WebP images online instantly."
    },
  
    compress: {
      "@context": "https://schema.org",
      "@type": "SoftwareApplication",
      name: "Online Image Compressor",
      operatingSystem: "All",
      applicationCategory: "MultimediaApplication",
      offers: { "@type": "Offer", price: "0", priceCurrency: "USD" },
      url: "https://theimageresizer.com/compress",
      description: "Compress and reduce file size of JPG, PNG, and WebP images without losing quality."
    },
  
    convert: {
      "@context": "https://schema.org",
      "@type": "SoftwareApplication",
      name: "Online Image Converter",
      operatingSystem: "All",
      applicationCategory: "MultimediaApplication",
      offers: { "@type": "Offer", price: "0", priceCurrency: "USD" },
      url: "https://theimageresizer.com/convert",
      description: "Convert images between JPG, PNG, and WebP formats instantly online."
    },
  
    crop: {
      "@context": "https://schema.org",
      "@type": "SoftwareApplication",
      name: "Online Image Crop Tool",
      operatingSystem: "All",
      applicationCategory: "MultimediaApplication",
      offers: { "@type": "Offer", price: "0", priceCurrency: "USD" },
      url: "https://theimageresizer.com/crop",
      description: "Crop images online with custom aspect ratios or social media presets."
    },
  
    blog: {
      "@context": "https://schema.org",
      "@type": "Blog",
      name: "TheImageResizer Blog",
      url: "https://theimageresizer.com/blog",
      description: "Guides and tutorials on resizing, compressing, converting, and optimizing images."
    },

    blogPost: (post) => ({
      "@context": "https://schema.org",
      "@type": "BlogPosting",
      headline: post.title,
      description: post.description,
      datePublished: post.date,
      author: {
        "@type": "Organization",
        name: "TheImageResizer",
      },
      publisher: {
        "@type": "Organization",
        name: "TheImageResizer",
        logo: {
          "@type": "ImageObject",
          url: "https://theimageresizer.com/logo.png", // update with your actual logo
        },
      },
      mainEntityOfPage: {
        "@type": "WebPage",
        "@id": `https://theimageresizer.com/blog/${post.slug}`,
      },
    }),
  };
  
  export default schemaConfig;
  