// src/services/imageService.js

// --- small helpers ---
export const blobToObjectURL = (blob) => URL.createObjectURL(blob);
export const revokeObjectURL = (url) => url && URL.revokeObjectURL(url);

function canvasToBlob(canvas, mimeType, quality) {
  return new Promise((resolve, reject) => {
    try {
      canvas.toBlob((b) => resolve(b), mimeType, quality);
    } catch (e) {
      reject(e);
    }
  });
}

function getTargetDimensions(img, targetW, targetH) {
  const w = Number(targetW) || 0;
  const h = Number(targetH) || 0;
  if (w > 0 && h > 0) return { width: w, height: h };
  if (w > 0) return { width: w, height: Math.round((img.height / img.width) * w) };
  if (h > 0) return { width: Math.round((img.width / img.height) * h), height: h };
  return { width: img.width, height: img.height };
}

function drawToCanvas(img, width, height) {
  const c = document.createElement("canvas");
  c.width = Math.max(1, Math.round(width));
  c.height = Math.max(1, Math.round(height));
  const ctx = c.getContext("2d");
  ctx.imageSmoothingEnabled = true;
  ctx.imageSmoothingQuality = "high";
  ctx.drawImage(img, 0, 0, c.width, c.height);
  return c;
}

async function resizeCanvas(canvas, width, height) {
  const c = document.createElement("canvas");
  c.width = Math.max(1, Math.round(width));
  c.height = Math.max(1, Math.round(height));
  const ctx = c.getContext("2d");
  ctx.imageSmoothingEnabled = true;
  ctx.imageSmoothingQuality = "high";
  ctx.drawImage(canvas, 0, 0, c.width, c.height);
  return c;
}

// --- main processing entry ---
/**
 * Process an image file with the given settings.
 * @param {File} file
 * @param {{format: 'jpg'|'png'|'webp', targetKB?: number, width?: number|string, height?: number|string}} settings
 * @returns {Promise<{blob: Blob, width: number, height: number, hitTarget: boolean}>}
 */
export async function processImageFile(file, settings) {
  const { format = "jpg", targetKB, width, height } = settings || {};
  const mime =
    format === "png" ? "image/png" : format === "webp" ? "image/webp" : "image/jpeg";

  // 1) load <img>
  const srcUrl = URL.createObjectURL(file);
  const img = await new Promise((res, rej) => {
    const i = new Image();
    i.onload = () => res(i);
    i.onerror = rej;
    i.src = srcUrl;
  });
  URL.revokeObjectURL(srcUrl);

  // 2) size we will render at
  const { width: outW, height: outH } = getTargetDimensions(img, width, height);
  let canvas = drawToCanvas(img, outW, outH);

  // 3) If no target size -> simple encode
  if (!targetKB || Number(targetKB) <= 0) {
    const q = mime === "image/jpeg" || mime === "image/webp" ? 0.9 : undefined;
    const blob = await canvasToBlob(canvas, mime, q);
    return { blob, width: canvas.width, height: canvas.height, hitTarget: false };
  }

  const targetBytes = Number(targetKB) * 1024;

  if (mime === "image/png") {
    // PNG doesn't honor quality; downscale until <= target or we give up
    let bestBlob = await canvasToBlob(canvas, mime);
    if (bestBlob.size <= targetBytes) {
      return { blob: bestBlob, width: canvas.width, height: canvas.height, hitTarget: true };
    }
    let scale = 0.92; // more aggressive than 0.95 so it converges faster
    for (let i = 0; i < 20; i++) {
      const w = Math.max(1, Math.round(canvas.width * scale));
      const h = Math.max(1, Math.round(canvas.height * scale));
      canvas = await resizeCanvas(canvas, w, h);
      const blob = await canvasToBlob(canvas, mime);
      if (blob.size <= targetBytes) {
        return { blob, width: w, height: h, hitTarget: true };
      }
      if (!bestBlob || blob.size < bestBlob.size) bestBlob = blob;
      scale *= 0.92;
    }
    // couldn't reach target; return best effort
    return { blob: bestBlob, width: canvas.width, height: canvas.height, hitTarget: false };
  }

  // JPEG / WebP  binary search on quality
  let lo = 0.1, hi = 0.95;
  let best = await canvasToBlob(canvas, mime, hi);
  if (best.size <= targetBytes) {
    return { blob: best, width: canvas.width, height: canvas.height, hitTarget: true };
  }
  let bestBlob = best, bestDiff = Math.abs(best.size - targetBytes);

  for (let i = 0; i < 22; i++) {
    const q = (lo + hi) / 2;
    const b = await canvasToBlob(canvas, mime, q);
    const diff = Math.abs(b.size - targetBytes);

    if (diff < bestDiff) {
      bestDiff = diff;
      bestBlob = b;
    }
    if (b.size > targetBytes) {
      hi = q; // too big  lower quality
    } else {
      lo = q; // too small  raise quality
    }

    if (diff / targetBytes < 0.03) {
      return { blob: b, width: canvas.width, height: canvas.height, hitTarget: true };
    }
  }
  return { blob: bestBlob, width: canvas.width, height: canvas.height, hitTarget: false };
}
