import './App.css';
import Layout from './components/layout/Layout.js';
import { Routes, Route } from "react-router-dom";

import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { trackPageView } from "./services/analyticsService";

import Home from "./pages/Home";
import Resize from "./pages/Resize";
import Compress from "./pages/Compress";
import Convert from "./pages/Convert";
import AmazonProductImageResizer from "./pages/AmazonProductImageResizer";
import LinkedinImageResizer from "./pages/LinkedinImageResizer";
import SpotifyImageMaker from "./pages/SpotifyImageMaker";
import ShopifyImageMaker from "./pages/ShopifyImageMaker";
import PassportPhotoMaker from "./pages/PassportPhotoMaker";
import Crop from "./pages/Crop";
import Blog from "./pages/Blog";
import BlogPost from "./pages/BlogPost";
import FAQ from "./pages/FAQ";
import Sitemap from "./pages/Sitemap";
import Terms from "./pages/Terms";
import Privacy from "./pages/Privacy";
import Disclaimer from "./pages/Disclaimer";
import About from "./pages/About";

function App() {

  const location = useLocation();

  useEffect(() => {
    // Track pageview whenever location changes
    trackPageView(location.pathname + location.search);
  }, [location]);

  return (
    <Layout>
      
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/resize" element={<Resize />} />
        <Route path="/compress" element={<Compress />} />
        <Route path="/convert" element={<Convert />} />
        <Route path="/amazon-product-image-resizer" element={<AmazonProductImageResizer />} />
        <Route path="/linkedin-image-resizer" element={<LinkedinImageResizer />} />
        <Route path="/spotify-image-maker" element={<SpotifyImageMaker />} />
        <Route path="/shopify-image-maker" element={<ShopifyImageMaker />} />
        <Route path="/passport-photo-maker" element={<PassportPhotoMaker />} />
        <Route path="/crop" element={<Crop />} />
        <Route path="/blog" element={<Blog />} />
        <Route path="/blog/:slug" element={<BlogPost />} />
        <Route path="/faq" element={<FAQ />} />
        <Route path="/sitemap" element={<Sitemap />} />
        <Route path="/terms" element={<Terms />} />
        <Route path="/privacy" element={<Privacy />} />
        <Route path="/disclaimer" element={<Disclaimer />} />
        <Route path="/about" element={<About />} />

      </Routes>
    </Layout>
  );
}

export default App;
