import React from 'react';

const FeatureGrid = ({ features }) => {
  return (
    <div className="mt-16 mb-8">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 max-w-6xl mx-auto">
        {features.map((feature, index) => (
          <div key={index} className="bg-white border rounded-2xl p-6 text-center shadow-md hover:shadow-lg transition-shadow duration-200">
            <div className="flex justify-center mb-4">
              <div className="w-12 h-12 bg-gray-50 border-2 border-gray-200 rounded-xl flex items-center justify-center">
                <div className="text-gray-700">
                  {feature.icon}
                </div>
              </div>
            </div>
            <h3 className="text-xl font-bold text-gray-900 mb-3">{feature.title}</h3>
            <p className="text-gray-600 text-sm leading-relaxed">{feature.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FeatureGrid;
