import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Meta from "../components/seo/Meta";
import Schema from "../components/seo/Schema";
import metaConfig from "../utils/metaConfig";
import schemaConfig from "../utils/schemaConfig";

export default function Blog() {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    fetch("/blog/blogIndex.json")
      .then((res) => res.json())
      .then((data) => {
        // Sort by date (newest first)
        const sorted = data.sort(
          (a, b) => new Date(b.date) - new Date(a.date)
        );
        setPosts(sorted);
      })
      .catch((err) => console.error("Error loading blog index:", err));
  }, []);

  const meta = metaConfig.blog;
  const schema = schemaConfig.blog;

  return (
    <>
    <Meta {...meta} />
    <Schema schema={schema} />
    <div className="max-w-4xl mx-auto p-6">
      {/* SEO */}
      

      {/* Page Title */}
      <h1 className="text-3xl font-bold mb-6">Our Blog</h1>

      {/* Blog List */}
      <div className="space-y-6">
        {posts.length > 0 ? (
          posts.map((post) => (
            <div
              key={post.slug}
              className="p-4 bg-white rounded-lg shadow hover:shadow-md transition"
            >
              <Link to={`/blog/${post.slug}`}>
                <h2 className="text-xl font-semibold text-blue-600 hover:underline">
                  {post.title}
                </h2>
              </Link>
              <p className="text-gray-500 text-sm">{post.date}</p>
              <p className="text-gray-700 mt-2">{post.description}</p>
            </div>
          ))
        ) : (
          <p className="text-gray-500">Loading posts...</p>
        )}
      </div>
    </div>
    </>
    
  );
}
