import React from 'react';
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";

const Sitemap = () => {
  const sitemapData = [
    {
      category: "Main Tools",
      description: "Core image processing tools for all your needs",
      links: [
        { name: "Home", url: "/", description: "Main page with all-in-one image processing tools" },
        { name: "Resize Images", url: "/resize", description: "Resize images to exact dimensions for any platform" },
        { name: "Compress Images", url: "/compress", description: "Reduce file size without losing quality" },
        { name: "Convert Images", url: "/convert", description: "Convert between JPG, PNG, and WebP formats" }
      ]
    },
    {
      category: "Platform-Specific Tools",
      description: "Specialized tools for popular platforms and marketplaces",
      links: [
        { name: "Amazon Image Maker", url: "/amazon-product-image-resizer", description: "Create Amazon-compliant product images with proper dimensions" },
        { name: "LinkedIn Image Maker", url: "/linkedin-image-resizer", description: "Optimize profile photos and cover images for LinkedIn" },
        { name: "Spotify Image Maker", url: "/spotify-image-maker", description: "Create perfect square artwork for Spotify playlists and albums" },
        { name: "Shopify Image Maker", url: "/shopify-image-maker", description: "Optimize product images for faster Shopify storefronts" },
        { name: "Passport Photo Maker", url: "/passport-photo-maker", description: "Create official passport and visa photos with correct dimensions" }
      ]
    },
    {
      category: "Content & Information",
      description: "Learn more about our tools and get support",
      links: [
        { name: "Blog", url: "/blog", description: "Tips, tutorials, and guides for image optimization" },
        { name: "FAQ", url: "/faq", description: "Frequently asked questions and answers" },
        { name: "About Us", url: "/about", description: "Learn about TheImageResizer and our mission" },
        { name: "Contact Us", url: "mailto:contact@attobite.in", description: "Get in touch with our support team" }
      ]
    },
    {
      category: "Legal & Policies",
      description: "Important legal information and policies",
      links: [
        { name: "Disclaimer", url: "/disclaimer", description: "Terms of service and usage disclaimer" },
        { name: "Terms of Service", url: "/terms", description: "Terms and conditions for using our service" },
        { name: "Privacy Policy", url: "/privacy", description: "How we collect, use, and protect your data" }
      ]
    }
  ];

  return (
    <div className="max-w-6xl mx-auto px-4 py-10">
      <div className="text-center mb-12">
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-4">Sitemap</h1>
        <p className="text-lg text-gray-600 max-w-3xl mx-auto">
          Find all pages and tools available on TheImageResizer. Our comprehensive sitemap helps you navigate to any tool or information you need.
        </p>
      </div>

      <div className="space-y-8">
        {sitemapData.map((section, sectionIndex) => (
          <div key={sectionIndex} className="bg-white border border-gray-200 rounded-2xl shadow-md p-6">
            <div className="mb-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-2">{section.category}</h2>
              <p className="text-gray-600">{section.description}</p>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {section.links.map((link, linkIndex) => (
                <div key={linkIndex} className="border border-gray-100 rounded-lg p-4 hover:shadow-md transition-shadow duration-200">
                  <a 
                    href={link.url} 
                    className="block group"
                    {...(link.url.startsWith('mailto:') ? {} : { target: "_blank", rel: "noopener noreferrer" })}
                  >
                    <h3 className="text-lg font-semibold text-gray-900 group-hover:text-blue-600 transition-colors duration-200 mb-2">
                      {link.name}
                    </h3>
                    <p className="text-sm text-gray-600 leading-relaxed">
                      {link.description}
                    </p>
                  </a>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>

      {/* Additional Information */}
      <div className="mt-12 bg-blue-50 border border-blue-200 rounded-2xl p-8 text-center">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Can't Find What You're Looking For?</h2>
        <p className="text-gray-600 mb-6">
          If you can't find the page or tool you need, feel free to reach out to our support team. We're here to help!
        </p>
        <a
          href="mailto:contact@attobite.in"
          className="inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-lg shadow-sm text-white bg-blue-600 hover:bg-blue-700 transition-colors duration-200"
        >
          Contact Support
        </a>
      </div>
    </div>
  );
};

export default Sitemap;
