import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";


// Simple Amazon defaults: longest side 2000px, JPG format recommended
const AmazonForm = ({ onSubmit }) => (
  <div className="mt-6">
    <p className="text-sm text-gray-600 mb-2">Amazon recommends at least 1600px on longest side (2000px ideal), JPG.</p>
    <button
      onClick={() => onSubmit({ width: 2000, height: undefined, format: "jpg", targetKB: undefined })}
      className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition"
    >
      Apply Amazon Recommended Settings
    </button>
  </div>
);

export default function AmazonProductImageResizer() {
  const features = featuresDescriptionConfig.amazon

  const steps = stepsConfig.amazon
  const meta = metaConfig.amazon

  return (
    <>
      <Meta {...meta} />
      <div className="max-w-5xl mx-auto px-4 py-10">
      <AdSlot slot={adSlots.amazon[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Amazon Product Image Resizer</h1>
        <p className="text-center text-gray-600 mb-8">Resize images to Amazon's recommended dimensions.</p>

        <ImageProcessorSection downloadName="amazon-image" normalizeSettings={(s) => ({ ...s, height: undefined, targetKB: undefined })}>
          {({ onApply, loading }) => (
            <>
              <AmazonForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>

        <AdSlot slot={adSlots.amazon[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.amazon[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={steps} title="Make Amazon-Ready Images in 4 Easy Steps" />

        {/* Bottom Ad */}
        <AdSlot slot={adSlots.amazon[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

