import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import ImageRequirementsForm from "../components/image/ImageRequirementsForm";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import schemaConfig from "../utils/schemaConfig";
import Schema from "../components/seo/Schema";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

export default function Convert() {
  const features = featuresDescriptionConfig.convert

  const steps = stepsConfig.convert
  const meta = metaConfig.convert
  const schema = schemaConfig.convert

  return (

    <>
      <Meta {...meta} />
      <Schema schema={schema} />
      <div className="max-w-5xl mx-auto px-4 py-10">
      <AdSlot slot={adSlots.convert[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Convert</h1>
        <p className="text-center text-gray-600 mb-8">Choose output format: JPG, PNG, or WebP.</p>

        <ImageProcessorSection
          downloadName="converted-image"
          normalizeSettings={(s) => ({ ...s, width: undefined, height: undefined, targetKB: undefined })}
        >
          {({ onApply, loading }) => (
            <>
              <ImageRequirementsForm onSubmit={onApply} showWidth={false} showHeight={false} showFormat={true} showTargetKB={false} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.convert[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.convert[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={steps} title="Change image format in 4 steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.convert[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

