import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from "../utils/stepsConfig";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

const PassportForm = ({ onSubmit }) => (
  <div className="grid gap-3 sm:grid-cols-3 mt-6">
    <button onClick={() => onSubmit({ width: 600, height: 600, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">US 2x2 in (600x600)</button>
    <button onClick={() => onSubmit({ width: 827, height: 1063, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">EU 35x45 mm (827x1063)</button>
    <button onClick={() => onSubmit({ width: 413, height: 531, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">IN 35x45 mm (413x531)</button>
  </div>
);

export default function PassportPhotoMaker() {
  const features = featuresDescriptionConfig.passport
  const passportSteps = stepsConfig.passport
  const meta = metaConfig.passport

  return (
    <>
      <Meta {...meta} />
      <div className="max-w-5xl mx-auto px-4 py-10">
        <AdSlot slot={adSlots.passport[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Passport Photo Maker</h1>
        <p className="text-center text-gray-600 mb-8">Generate passport photos to standard sizes.</p>

        <ImageProcessorSection downloadName="passport-photo">
          {({ onApply, loading }) => (
            <>
              <PassportForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>

        <AdSlot slot={adSlots.passport[1]} style={{ margin: "20px 0" }} />
        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.passport[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={passportSteps} title="Make Passport Photos in 4 Easy Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.passport[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

