import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

// LinkedIn presets (examples): Profile 400x400, Cover 1584x396, Post 1200x1200
const LinkedInForm = ({ onSubmit }) => (
  <div className="grid gap-3 sm:grid-cols-3 mt-6">
    <button
      onClick={() => onSubmit({ width: 400, height: 400, format: "jpg" })}
      className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition"
    >
      Profile 400x400
    </button>
    <button
      onClick={() => onSubmit({ width: 1584, height: 396, format: "jpg" })}
      className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition"
    >
      Cover 1584x396
    </button>
    <button
      onClick={() => onSubmit({ width: 1200, height: 1200, format: "jpg" })}
      className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition"
    >
      Post 1200x1200
    </button>
  </div>
);

export default function LinkedinImageResizer() {
  const features = featuresDescriptionConfig.linkedin

  const linkedinSteps = stepsConfig.linkedin
  const meta = metaConfig.linkedin

  return (
    <>
      <Meta {...meta} />
      <div className="max-w-5xl mx-auto px-4 py-10">
        <AdSlot slot={adSlots.linkedin[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">LinkedIn Image Resizer</h1>
        <p className="text-center text-gray-600 mb-8">Quickly format images for LinkedIn profile, cover, and posts.</p>

        <ImageProcessorSection downloadName="linkedin-image" normalizeSettings={(s) => ({ ...s, targetKB: undefined })}>
          {({ onApply, loading }) => (
            <>
              <LinkedInForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.linkedin[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.linkedin[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={linkedinSteps} title="Create LinkedIn-Ready Images in 4 Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.linkedin[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

