import React, { useState } from 'react';

const Navigation = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <nav className="bg-white shadow-sm border-b border-gray-200 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-14 sm:h-16">
          {/* Logo */}
          <div className="flex-shrink-0">
            <a href="/" className="flex items-center gap-2">
              <img src="/favicon-32x32.png" alt="TheImageResizer logo" className="h-6 w-6 sm:h-7 sm:w-7" />
              <span className="text-lg sm:text-xl lg:text-2xl font-bold text-gray-900">
                TheImageResizer
              </span>
            </a>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:block">
            <div className="ml-10 flex items-baseline space-x-8">
              <a
                href="/"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Home
              </a>
              <a
                href="/resize"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Resize
              </a>
              <a
                href="/compress"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Compress
              </a>
              <a
                href="/convert"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Convert
              </a>
              <a
                href="/crop"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Crop
              </a>
              <a
                href="/blog"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                Blog
              </a>
              <a
                href="/faq"
                className="text-gray-900 hover:text-blue-600 px-3 py-2 text-sm font-medium transition-colors duration-200"
              >
                FAQ
              </a>
            </div>
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <button
              onClick={toggleMenu}
              className="inline-flex items-center justify-center p-3 rounded-md text-gray-900 hover:text-blue-600 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-blue-500 transition-colors duration-200 touch-manipulation"
              aria-expanded="false"
            >
              <span className="sr-only">Open main menu</span>
              {/* Hamburger icon */}
              <svg
                className={`${isMenuOpen ? 'hidden' : 'block'} h-6 w-6`}
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
              {/* Close icon */}
              <svg
                className={`${isMenuOpen ? 'block' : 'hidden'} h-6 w-6`}
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Navigation Menu */}
      <div className={`${isMenuOpen ? 'block' : 'hidden'} md:hidden transition-all duration-300 ease-in-out`}>
        <div className="px-4 pt-2 pb-4 space-y-1 bg-white border-t border-gray-200 shadow-lg">
          <a
            href="/"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Home
          </a>
          <a
            href="/resize"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Resize
          </a>
          <a
            href="/compress"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Compress
          </a>
          <a
            href="/convert"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Convert
          </a>
          <a
            href="/crop"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Crop
          </a>
          <a
            href="/blog"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            Blog
          </a>
          <a
            href="/faq"
            className="text-gray-900 hover:text-blue-600 hover:bg-gray-50 block px-4 py-3 text-base font-medium rounded-md transition-all duration-200 touch-manipulation"
            onClick={() => setIsMenuOpen(false)}
          >
            FAQ
          </a>
        </div>
      </div>
    </nav>
  );
};

export default Navigation;
