// src/services/analyticsService.js
import ReactGA from "react-ga4";

// Initialize Google Analytics
export const initGA = (measurementId) => {
  ReactGA.initialize(measurementId);
  console.log("Google Analytics initialized:", measurementId);
};

// Track page views
export const trackPageView = (path) => {
  ReactGA.send({ hitType: "pageview", page: path });
  console.log("Pageview tracked:", path);
};

// Track custom events
export const trackEvent = ({ category, action, label, value }) => {
  ReactGA.event({
    category,
    action,
    label,
    value,
  });
  console.log("Event tracked:", category, action, label, value);
};

// Track specific image actions
export const trackImageUpload = () => {
  trackEvent({ category: "Image", action: "Upload" });
};

export const trackImageResize = (format, targetSize) => {
  trackEvent({
    category: "Image",
    action: "Resize",
    label: format,
    value: targetSize,
  });
};

export const trackImageDownload = (format) => {
  trackEvent({
    category: "Image",
    action: "Download",
    label: format,
  });
};
