// src/components/image/ImageUploader.js
import React from "react";
import { trackImageUpload } from "../../services/analyticsService";

const ImageUploader = ({ onImageSelect }) => {
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && file.type.startsWith("image/")) {
      onImageSelect(file);
      trackImageUpload();
    } else {
      alert("Please upload a valid image file");
    }
  };

  return (
    <div className="max-w-md mx-auto p-6 bg-white border rounded-2xl shadow-md text-center">
      <label
        htmlFor="file-upload"
        className="cursor-pointer flex flex-col items-center justify-center border-2 border-dashed border-gray-300 rounded-xl p-6 hover:border-blue-500 transition-colors duration-200"
      >
        <svg
          className="w-10 h-10 text-gray-400 mb-2"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M4 16v2a2 2 0 002 2h12a2 2 0 002-2v-2M12 12V4m0 0l-4 4m4-4l4 4"
          />
        </svg>
        <p className="text-gray-600">Click to upload or drag and drop</p>
        <input
          id="file-upload"
          type="file"
          accept="image/*"
          className="hidden"
          onChange={handleFileChange}
        />
      </label>
    </div>
  );
};

export default ImageUploader;
