import React from 'react';
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from '../utils/stepsConfig';
import featuresDescriptionConfig from '../utils/featuresDescriptionConfig';
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import schemaConfig from '../utils/schemaConfig';
import Schema from '../components/seo/Schema';
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

// Placeholder crop form; integrate a cropper later
const CropForm = ({ onSubmit }) => (
  <div className="mt-6">
    <p className="text-sm text-gray-600 mb-2">Cropping UI coming soon. For now, apply a center-crop size.</p>
    <div className="grid gap-3 sm:grid-cols-2">
      <button onClick={() => onSubmit({ width: 1024, height: 1024, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Square 1024x1024</button>
      <button onClick={() => onSubmit({ width: 1920, height: 1080, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">16:9 1920x1080</button>
    </div>
  </div>
);

const Crop = () => {
  const features = featuresDescriptionConfig.crop

  const steps = stepsConfig.crop

  const meta = metaConfig.crop

  const schema = schemaConfig.crop

  return (
    <>
      <Meta {...meta} />
      <Schema schema={schema} />
      <div className="max-w-4xl mx-auto px-4 py-8">
        <AdSlot slot={adSlots.crop[0]} style={{ margin: "20px 0" }} />

        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Crop Image</h1>
        <p className="text-center text-gray-600 mb-8">Crop images quickly and precisely to remove unwanted areas, focus on the subject, and improve composition. Perfect for resizing photos for social media, presentations, or personal projects.</p>
        <ImageProcessorSection downloadName="cropped-image">
          {({ onApply, loading }) => (
            <>
              <CropForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.crop[1]} style={{ margin: "20px 0" }} />

        <FeatureGrid features={features} />

        <AdSlot slot={adSlots.crop[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={steps} title="Make Amazon-Ready Images in 4 Easy Steps" />

        {/* Bottom Ad */}
        <AdSlot slot={adSlots.crop[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
};

export default Crop;


