// src/components/Meta.js
import React from "react";
import { Helmet } from "react-helmet-async";

const Meta = ({ title, description, keywords, auto_ads }) => {
  console.log(title);
  console.log(description);
  console.log(keywords);

  return (
    <Helmet>
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:type" content="website" />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="google-adsense-platform-account" content="ca-pub-3822883775796238" />
      <meta name="google-adsense-platform-domain" content="theimageresizer.com" />
      <meta name="google-adsense-auto-ads" content={auto_ads} />
    </Helmet>
  );
};

export default Meta;
