import React from "react";
import ImageProcessorSection from "../components/image/ImageProcessorSection";
import FeatureGrid from "../components/ui/FeatureGrid";
import ProcessSteps from "../components/ui/ProcessSteps";
import stepsConfig from "../utils/stepsConfig";
import featuresDescriptionConfig from "../utils/featuresDescriptionConfig";
import Meta from "../components/seo/Meta";
import metaConfig from "../utils/metaConfig";
import { adSlots } from "../utils/adSlots";
import AdSlot from "../components/ads/AdSlot";

const ShopifyForm = ({ onSubmit }) => (
  <div className="grid gap-3 sm:grid-cols-3 mt-6">
    <button onClick={() => onSubmit({ width: 2048, height: 2048, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Product 2048x2048</button>
    <button onClick={() => onSubmit({ width: 1024, height: 1024, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Thumbnail 1024x1024</button>
    <button onClick={() => onSubmit({ width: 1200, height: 628, format: 'jpg' })} className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition">Social 1200x628</button>
  </div>
);

export default function ShopifyImageMaker() {
  const features = featuresDescriptionConfig.shopify

  const shopifySteps = stepsConfig.shopify

  const meta = metaConfig.shopify


  return (
    <>
      <Meta {...meta} />
      <div className="max-w-5xl mx-auto px-4 py-10">
        <AdSlot slot={adSlots.shopify[0]} style={{ margin: "20px 0" }} />
        <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 text-center mb-6">Shopify Image Maker</h1>
        <p className="text-center text-gray-600 mb-8">Create product images optimized for Shopify.</p>

        <ImageProcessorSection downloadName="shopify-image">
          {({ onApply, loading }) => (
            <>
              <ShopifyForm onSubmit={onApply} />
              {loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
            </>
          )}
        </ImageProcessorSection>
        <AdSlot slot={adSlots.shopify[1]} style={{ margin: "20px 0" }} />
        <FeatureGrid features={features} />
        <AdSlot slot={adSlots.shopify[2]} style={{ margin: "20px 0" }} />
        <ProcessSteps steps={shopifySteps} title="Optimize Shopify Images in 4 Steps" />
        {/* Bottom Ad */}
        <AdSlot slot={adSlots.shopify[3]} style={{ marginTop: "30px" }} />
      </div>
    </>

  );
}

