// src/components/image/ImageRequirementsForm.js
import React, { useState } from "react";

const ImageRequirementsForm = ({ onSubmit, defaults, showWidth = true, showHeight = true, showFormat = true, showTargetKB = true }) => {
  const [width, setWidth] = useState(defaults?.width || "");
  const [height, setHeight] = useState(defaults?.height || "");
  const [format, setFormat] = useState(defaults?.format || "jpg");
  const [targetKB, setTargetKB] = useState(defaults?.targetKB || "");

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit({
      width: width ? Number(width) : undefined,
      height: height ? Number(height) : undefined,
      format,
      targetKB: targetKB ? Number(targetKB) : undefined,
    });
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="grid gap-4 sm:grid-cols-2 bg-gray-50 p-6 rounded-lg shadow mt-6"
    >
      {showWidth && (
        <div>
          <label className="block text-sm font-medium text-gray-700">Width (px)</label>
          <input
            type="number"
            value={width}
            onChange={(e) => setWidth(e.target.value)}
            placeholder="Leave empty to auto"
            className="mt-1 w-full border rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-500"
          />
        </div>
      )}

      {showHeight && (
        <div>
          <label className="block text-sm font-medium text-gray-700">Height (px)</label>
          <input
            type="number"
            value={height}
            onChange={(e) => setHeight(e.target.value)}
            placeholder="Leave empty to auto"
            className="mt-1 w-full border rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-500"
          />
        </div>
      )}

      {showFormat && (
        <div>
          <label className="block text-sm font-medium text-gray-700">Format</label>
          <select
            value={format}
            onChange={(e) => setFormat(e.target.value)}
            className="mt-1 w-full border rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-500"
          >
            <option value="jpg">JPG</option>
            <option value="png">PNG</option>
            <option value="webp">WebP</option>
          </select>
        </div>
      )}

      {showTargetKB && (
        <div>
          <label className="block text-sm font-medium text-gray-700">
            Target size (KB)
          </label>
          <input
            type="number"
            min="1"
            value={targetKB}
            onChange={(e) => setTargetKB(e.target.value)}
            placeholder="e.g., 200"
            className="mt-1 w-full border rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-500"
          />
        </div>
      )}

      <div className="sm:col-span-2">
        <button
          type="submit"
          className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition"
        >
          Apply Settings
        </button>
      </div>
    </form>
  );
};

export default ImageRequirementsForm;
