// src/components/image/ImageDownloadButton.js
import React, { useState, useEffect } from "react";
import { trackImageDownload } from "../../services/analyticsService";
import AdSlot from "../ads/AdSlot";

const ImageDownloadButton = ({ url, filename = "image", extension = "jpg" }) => {
  const [showPopup, setShowPopup] = useState(false);
  const [countdown, setCountdown] = useState(5);

  useEffect(() => {
    let interval, timeout;

    if (showPopup) {
      setCountdown(5);

      // Countdown updater
      interval = setInterval(() => {
        setCountdown((prev) => (prev > 0 ? prev - 1 : 0));
      }, 1000);

      // Trigger download exactly once after 5s
      timeout = setTimeout(() => {
        triggerDownload();
      }, 5000);
    }

    // Cleanup interval/timeout when popup closes or reopens
    return () => {
      clearInterval(interval);
      clearTimeout(timeout);
    };
  }, [showPopup]);

  const triggerDownload = () => {
    if (!url) return;

    const link = document.createElement("a");
    link.href = url;
    link.download = `${filename}.${extension}`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    trackImageDownload(extension);

    // Reset popup after download
    setShowPopup(false);
    setCountdown(5);
  };

  const handleDownloadClick = () => {
    if (!url) return;
    setShowPopup(true);
  };

  if (!url) return null;

  return (
    <div className="text-center mt-4">
      <button
        onClick={handleDownloadClick}
        className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition"
      >
        Download Processed Image
      </button>

      {/* Popup Modal */}
      {showPopup && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded-lg shadow-lg max-w-md w-full text-center relative">
            <button
              onClick={() => setShowPopup(false)}
              className="absolute top-2 right-2 text-gray-500 hover:text-gray-700"
            >
              
            </button>

            <h2 className="text-lg font-semibold mb-2">Preparing your download...</h2>
            <p className="text-gray-500 text-sm mb-4">
              Your file will be ready in <strong>{countdown}</strong> seconds.
            </p>

            {/* Ad inside popup */}
            <div className="mb-4">
              <AdSlot slot="4379679113" style={{ margin: "10px auto" }} />
            </div>

            <p className="text-gray-400 text-xs mt-4">
              Download will start automatically.
            </p>
          </div>
        </div>
      )}
    </div>
  );
};

export default ImageDownloadButton;
