// src/components/ads/AdSlot.js
import React, { useEffect, useRef } from "react";

const AdSlot = ({ slot, format = "auto", responsive = "true", style = {} }) => {
  const adRef = useRef(null);

  useEffect(() => {
    if (!window.adsbygoogle || !adRef.current) return;

    if (!adRef.current.getAttribute("data-adsbygoogle-status")) {
      try {
        (window.adsbygoogle = window.adsbygoogle || []).push({});
        console.warn("AdSense push done")
      } catch (e) {
        console.warn("AdSense push skipped:", e.message);
      }
    }
  }, []);

  return (
    <ins
      className="adsbygoogle"
      style={{ display: "block", textAlign: "center", ...style }}
      data-ad-client="ca-pub-3822883775796238"
      data-ad-slot={slot}
      data-ad-format={format}
      data-full-width-responsive={responsive}
      ref={adRef}
    ></ins>
  );
};

export default AdSlot;
