// src/components/image/ImageProcessorSection.js
import React, { useEffect, useState } from "react";
import ImageUploader from "./ImageUploader";
import ImagePreview from "./ImagePreview";
import ImageDownloadButton from "./ImageDownloadButton";
import { processImageFile, blobToObjectURL, revokeObjectURL } from "../../services/imageService";

/**
 * Reusable image processing section that handles upload, preview, form, and download.
 * Props:
 * - downloadName: string (base filename without extension)
 * - normalizeSettings?: (settings) => settings (allows per-page tweaks before processing)
 * - children?: function render prop receiving { onApply, file, loading }
 */
const ImageProcessorSection = ({
  downloadName = "processed-image",
  normalizeSettings,
  children,
}) => {
  const [file, setFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState("");
  const [downloadUrl, setDownloadUrl] = useState("");
  const [loading, setLoading] = useState(false);
  const [lastFormat, setLastFormat] = useState("jpg");

  useEffect(() => {
    revokeObjectURL(previewUrl);
    revokeObjectURL(downloadUrl);
    if (!file) {
      setPreviewUrl("");
      setDownloadUrl("");
      return;
    }
    const url = URL.createObjectURL(file);
    setPreviewUrl(url);
    setDownloadUrl("");
    return () => revokeObjectURL(url);
  }, [file]);

  const handleApply = async (settings) => {
    if (!file) {
      alert("Please upload an image first");
      return;
    }
    setLoading(true);
    try {
      const next = normalizeSettings ? normalizeSettings(settings) : settings;
      if (next?.format) setLastFormat(next.format);
      const { blob } = await processImageFile(file, next);
      const outUrl = blobToObjectURL(blob);
      revokeObjectURL(previewUrl);
      revokeObjectURL(downloadUrl);
      setPreviewUrl(outUrl);
      setDownloadUrl(outUrl);
    } catch (e) {
      console.error(e);
      alert("Failed to process image.");
    } finally {
      setLoading(false);
    }
  };

  const handleRemove = () => {
    revokeObjectURL(previewUrl);
    revokeObjectURL(downloadUrl);
    setFile(null);
    setPreviewUrl("");
    setDownloadUrl("");
  };

  return (
    <div>
      {file ? (
        <ImagePreview src={previewUrl} onRemove={handleRemove} />
      ) : (
        <ImageUploader onImageSelect={setFile} />
      )}

      {file && children && children({ onApply: handleApply, file, loading })}
      {file && !children && loading && <p className="text-center text-gray-500 mt-4">Processing</p>}
      {file && <ImageDownloadButton url={downloadUrl} filename={downloadName} extension={lastFormat} />}
    </div>
  );
};

export default ImageProcessorSection;


